<template>
  <div v-if="visible" class="map-dialog">
    <div class="map-dialog-bg"></div>
    <div class="map-dialog-wrap">
      <div class="map-dialog-wrp_head">
        <i class="el-icon-close" @click="close"></i>
      </div>
      <div class="map-dialog-wrp_body">
        <slot></slot>
      </div>
    </div>

  </div>
</template>

<script>


  export default {
    name: 'About',
    props:['visible'],
    data () {
      return {

      }
    },
    methods: {

      close(){
        this.$emit("close",this.visible);
        this.visible = null;
        }

    }
  }
</script>

<style scoped>
  .company-profile>p{
    line-height: 1.8;
    font-size:16px;
    color:#666;
    margin-top:20px;
  }

</style>
<style>
  .bm-view {
    width: 100%;
    height: 400px;

    border:1px solid #f0f;
  }
  .map-dialog{
    position:fixed;
    width:100%;
    height:100%;
    top:0;
    left:0;
  }
  .map-dialog-bg{
    position:absolute;
    width:100%;
    height:100%;
    top:0;
    left:0;
    background:rgba(0,0,0,0.5);
    z-index:1000;
  }
  .map-dialog-wrap{
    position:absolute;
    width:500px;
    height:500px;
    top:100px;
    left:100px;
    z-index:1001;
  }
  .map-dialog-wrp_body{
    padding:0 20px 20px 20px;
    background:#fff;
  }
  .map-dialog-wrp_head{
    position:relative;
    height:40px;
  }
  .el-icon-close{
    position:absolute;
    font-size: 30px;
    color:#fff;
    right:0;
    cursor: pointer;
  }
</style>



























