<template>
  <div class="y-header">
    <div class="y-header-wrap">
    <div class="main-container clearfloat">
      <div class="logo"><img src="../../assets/logo.png" /></div>
      <div class="login">
        <el-button size="small" type="danger" class="login-btn">登录</el-button>
      </div>
      <el-menu :default-active="activeIndex" active-text-color="#ec6c6a" class="el-menu-demo ymenu " mode="horizontal">

        <el-menu-item :index="m.path" @click.native="go(m.path)" v-for="m in menu" :key="m.id">{{m.name}}</el-menu-item>

    </el-menu>
    </div>
    </div>
  </div>

</template>

<script>
  import Bus from '../../assets/bus.js'
const menuData = [{
      name:'首页',
      path:'/'
  },{
    name:'物联网卡',
    path:'/sim'
  }, {
    name:'行业案例',
    path:'/industrycase'
  }, {
    name:'行业解决方案',
    path:'/scheme'
  },{
  name:'API文档',
  path:'/api'
  },{
    name:'关于我们',
    path:'/about'
  }]

export default {
  name: 'YHeader',
  data() {
    return {
      activeIndex: this.$route.path,
      menu:menuData,
    };
  },
  mounted:function(){
    var _this = this;
    Bus.$on('watchPath',function(msg){
      _this.activeIndex  = msg

    })
  },
  methods: {
    go (url) {
      if (url) {
        this.$router.push(`${url}`)
      } else {
        return false
      }
      Bus.$emit('watchPath',url);
    }
  }
}
</script>

<style scoped>
  .y-header{
    height:60px;
  }
  .y-header-wrap{
    position:fixed;
    width:100%;
    height:60px;
    background-color: #fff;
    border-bottom:1px solid #ddd;
    top:0;
    z-index: 1000;
  }
  .logo{
    display:inline-block;
    width: 20px;
    height:60px;
    line-height:60px;
  }
  .logo>img{
    display:inline-block;
    width:100%;
    vertical-align: middle;
  }
  .ymenu.el-menu--horizontal{
    float:right;
  }

  .y-header .login{
    display:inline-block;
    float:right;
    height:60px;
    line-height:60px;
    margin-left:40px;
  }
  .y-header  .login-btn{
    border:1px solid #ec6c6a;
    color: #f97270;
    background-color:#fff !important;
  }
  .y-header  .login-btn:hover{
    background-color: #ffe4e9 !important;

  }


</style>
<style>




</style>



