import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home/Home'
import Sim from '@/pages/sim/Sim'
import Scheme from '@/pages/scheme/Scheme'
import About from '@/pages/about/About'
import Industrycase from '@/pages/industrycase/Industrycase'
import Api from '@/pages/api/Api'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/sim',
      name: 'Sim',
      component: Sim
    },
    {
      path: '/scheme',
      name: 'Scheme',
      component: Scheme
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/industrycase',
      name: 'Industrycase',
      component: Industrycase
    },
    {
      path: '/api',
      name: 'Api',
      component: Api
    }
  ]
})



