<template>
  <div class="y-footer">
    <div class="main-container">
      <el-row :gutter="5">
        <el-col :span="6" >
          <div class="grid-content ">
            <p class="fs16">联系我们</p>
            <p class="mt10 color-ccc">地址：深圳市宝安区银田路宝安智谷C座110室</p>
            <p class="mt10 color-ccc">邮箱：ning.liu@yrt-tech.com</p>
          </div>
        </el-col>
        <el-col :span="6" style="margin-left:100px;">
          <div class="grid-content footer-link" >
            <p class="fs16">相关链接</p>
            <el-col :span="8" class="link" @click.native="go('/sim')">物联网卡</el-col>
            <el-col :span="8" class="link" @click.native="go('/scheme')">行业决解方案</el-col>
            <el-col :span="8" class="link" @click.native="go('/industrycase')">行业案例</el-col>
            <el-col :span="8" class="link" @click.native="go('/api')">API文档</el-col>
            <el-col :span="8" class="link" @click.native="go('/about')" >关于我们</el-col>
          </div>
        </el-col>
      </el-row>
      <div class="copy">
        <p>Copyright © 深圳市易睿通科技有限公司<span class="pl40">版权所有：粤ICP备16084487号-1</span></p>
      </div>
    </div>
  </div>
</template>

<script>
  import Bus from '../../assets/bus.js'
  export default {
    name: 'YFooter',
    data() {
      return {

      };
    },
    methods: {
      go (url) {
        if (url) {
          this.$router.push(`${url}`)
        } else {
          return false
        }
        Bus.$emit('watchPath',url);
      }
    }
  }
</script>

<style scoped>
.y-footer{
  padding:60px 0 30px 0;
  background: #2a3241;
  color:#fff;
}
.footer-link .link{
  display:inline-block;
  cursor: pointer;
  margin-top:10px;
  color:#ccc;
}
.footer-link .link:hover{
    color:#fff;
  }
.copy{
  border-top:1px solid #ccc;
  margin-top:40px;
  padding-top:10px;
  color:#ccc;
}
</style>



