<script>
  import YHighlight from '../../compontents/highlight/Highlight.vue'
  export default {
    name: 'Api',
    components:{
      YHighlight
    },
    data() {
      return {
        filterText: '',
        data2: [ {
          id: 's1',
          label: '一级 1',
          children: [{
            id: 's1-1',
            label: '二级 1-1'
          }, {
            id: 's1-2',
            label: '二级 1-2'
          }]
        }, {
          id: 's2',
          label: '一级 2',
          children: [{
            id: 's2-1',
            label: '二级 2-1'
          }, {
            id: 's2-2',
            label: '二级 2-2'
          }]
        }, {
          id: 's3',
          label: '一级 3',
          children: [{
            id: 's3-1',
            label: '二级 3-1'
          }, {
            id: 's3-2',
            label: '二级 3-2'
          }]
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        }
      };


    },
    watch: {
      filterText(val) {
        this.$refs.leftMenu.filter(val);
      }
    },
    mounted() {


    },
    methods: {
      ctlExpandedKeys(val){
        for(var i=0;i<this.$refs.leftMenu.store._getAllNodes().length;i++){
          this.$refs.leftMenu.store._getAllNodes()[i].expanded=false;
        }
        this.key.splice(0,1,val);
      },
      getCheckedKeys() {
        var dd = this.$refs.leftMenu.getCurrentKey()
        if(this.$refs[dd]){
          this.txt = this.$refs[dd].offsetTop
        }
        this.$nextTick(() => {
            var total = this.$refs[dd].offsetTop
          document.body.scrollTop=total
          document.documentElement.scrollTop=total
          window.pageYOffset =total
        });
      },
      filterNode(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
      }

    }
  }
</script>
<style >
  .left-menu{
    position:fixed;
    z-index: 100;
    margin-top:60px;
  }
  .api-content{
    border-left:1px solid #ddd;
    padding-left:10px;
  }
</style>





