<template>
  <div>
    <el-carousel  class="banner" height="550px" arrow="never" indicator-position="none">
      <el-carousel-item >
        <img src="../../assets/banner7.jpg"/>
      </el-carousel-item>
    </el-carousel>
    <div class="pt60 pb60" >
      <div class="main-container">
        <div class="center fs32" >公司简介</div>
        <el-row :gutter="24">
          <el-col  >
            <div class="company-profile">
            <p>深圳市易睿通科技有限公司，成立于2016年，是一家有着丰富客户资源和行业经验，提供物联网专用网络解决方案的科技公司。</p>
            <p>公司聚焦车联网、智能穿戴设备、无线监控等行业，推广物联网解决方案，提供专业化的物联网自有运营平台，打造物联网“云-管-端”全方位的体系架构。</p>
            <p>公司密切协同各大通信运营商及专业公司，以开放、合作、共享的发展理念，广泛开展国际、国内企业合作，以市场化机制独立运作，力争成为立足全国，服务全球的物联网领先企业，推动物联网在各行业的规模应用。</p>
            </div>
          </el-col>
        </el-row>
        <div class="pt60 pb60" >
          <baidu-map class="bm-view" :center="{lng: lng, lat: lat}" :zoom="20" v-if="visible">
            <el-button type="primary" @click="visible=false">x</el-button>
            <bm-marker :position="{lng: lng, lat:lat }"   @click="infoWindowOpen" animation="BMAP_ANIMATION_BOUNCE">
              <bm-info-window :show="show" @close="infoWindowClose" @open="infoWindowOpen">{{address}}</bm-info-window>
              <bm-label :content="address" :labelStyle="{color: '#ec6c6a', fontSize : '18px'}" :offset="{width: -35, height: 30}"/>
            </bm-marker>
          </baidu-map>
        </div>
        <el-button type="primary" @click="getPosA">智谷C座</el-button>
        <el-button type="primary" @click="getPosB">智谷E座</el-button>
        <br>
        <br>



<map-dialog :visible="visibleTest"  @close="visibleTest=false">
  <baidu-map class="bm-view" :center="{lng: lng, lat: lat}" :zoom="20" >
    <bm-marker :position="{lng: lng, lat:lat }"   @click="infoWindowOpen" animation="BMAP_ANIMATION_BOUNCE">
      <bm-info-window :show="show" @close="infoWindowClose" @open="infoWindowOpen">{{address}}</bm-info-window>
      <bm-label :content="address" :labelStyle="{color: '#ec6c6a', fontSize : '18px'}" :offset="{width: -35, height: 30}"/>
    </bm-marker>
  </baidu-map>
</map-dialog>

        <el-button type="primary" @click="getPosA2">智谷C座2</el-button>
        <el-button type="primary" @click="getPosB2">智谷E座2</el-button>
        <br>
        {{txt}}


      </div>
    </div>



  </div>
</template>

<script>

  import MapDialog from '../../compontents/mapdialog/MapDialog'
  export default {
    name: 'About',
    components: {
      MapDialog
    },
    data () {
      return {
        visible:false,
        visibleTest:false,
        show: false,
        txt:'000',
        lng:'113.865658',
        lat:'22.58948',
        address:'深圳市易睿通科技有限公司'
      }
    },
    methods: {
      infoWindowClose () {
        this.show = false
      },
      infoWindowOpen () {
        this.show = true
      },
      getPosA () {
          this.visible = true
        this.txt = '智谷C座'
        this.lng = '113.865577'
        this.lat = '22.588929'
        this.address = '智谷C座'
      },
      getPosB () {
        this.visible = true
        this.txt = '智谷E座'
        this.lng = '113.86529'
        this.lat = '22.589547'
        this.address = '智谷E座'
      },
      getPosA2 () {
        this.visibleTest = true
        this.txt = '智谷C座'
        this.lng ='113.865577'
        this.lat ='22.588929'
        this.address ='智谷C座'
      },
      getPosB2 () {
        this.visibleTest = true
        this.txt = '智谷E座'
        this.lng = '113.86529'
        this.lat = '22.589547'
        this.address = '智谷E座'
      }
    }
  }
</script>

<style scoped>
.company-profile>p{
  line-height: 1.8;
  font-size:16px;
  color:#666;
  margin-top:20px;
}

</style>
<style>
  .bm-view {
    width: 100%;
    height: 400px;

    border:1px solid #f0f;
  }
</style>



